/* General Container */
.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

/* Headings */
h1 {
  text-align: center;
  margin-bottom: 20px;
}

/* Entry List */
.entries {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.entry-row {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  align-items: center;
}

.entry-row input[type="date"],
.entry-row input[type="number"],
.entry-row input[type="text"] {
  flex: 1 1 150px;
  padding: 8px;
  font-size: 14px;
}

.dollar-input {
  display: flex;
  flex: 1 1 150px;
}

.dollar-sign {
  display: inline-block;
  padding: 8px;
  background: #eee;
  border: 1px solid #ccc;
  border-right: none;
  font-weight: bold;
  font-size: 14px;
}

.dollar-input input {
  flex: 1;
  border: 1px solid #ccc;
  padding: 8px;
  font-size: 14px;
}

/* Buttons */
.add-btn,
.remove-btn,
.calculate-btn {
  cursor: pointer;
  margin-top: 20px;
  padding: 8px 12px;
  font-size: 14px;
  border: none;
  border-radius: 5px;
}

.add-btn {
  background-color: #007bff;
  color: white;
}

.remove-btn {
  background-color: #dc3545;
  color: white;
}

.calculate-btn.enabled {
  background-color: #28a745;
  color: white;
}

.calculate-btn.disabled {
  background-color: #ccc;
  cursor: not-allowed;
  color: #666;
}

/* Results */
.results {
  margin-top: 20px;
}

.result-entry {
  background-color: #f9f9f9;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
  word-wrap: break-word;
}

/* Responsive */
@media (max-width: 600px) {
  .entry-row {
    flex-direction: column;
    align-items: stretch;
  }

  .dollar-input {
    flex-direction: row;
  }

  .dollar-input input {
    min-width: 0;
  }

  .entry-row input,
  .dollar-input input {
    font-size: 16px;
    padding: 10px;
  }

  .add-btn,
  .remove-btn,
  .calculate-btn {
    width: 100%;
    margin-top: 5px;
    font-size: 16px;
  }
}
